body {
  font-size: 12px;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Cool Font";
  src: url(/static/media/courier_prime_code-webfont.ae05572a.woff) format('woff');
  unicode-range: U+30-39;
}

body {
  font-family: Arial;
}

.is-downloading {
  background-color: rgba(255, 255, 255, 0.8);
  height: 100%;
  width: 100%;
  position: absolute;
  font-size: 25px;
  text-align: center;
}
.is-downloading div {
  margin-top: 250px;
  background-color: rgba(255,255,255,0.9);
}

.filter-pane {
  font-family: "Cool Font", Arial;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px; /* calc(10px + 2vmin); */
  color: white;
}

.App-link {
  color: #61dafb;
}

.current {
  background-color: #ccffff;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  width: 100%;
  overflow: scroll;
}

.filter-trigger {
  position: absolute;
  right: 0;
  top: 0
}

.filter-trigger:hover .filter-pane {
  /* display: block; */
}

:root {
  --subtle-green: #dde6e1;
}

button {
  padding: 15px;
  min-width: 150px;
}

.thumb {
  width: 100%;
  padding: 15px;
}

.disclaimer {
  margin: 10px 0;
  font-size: 12px;
  color: grey;
  font-style: italic;
}

.filter-pane {
  font-size: 1.3em;
  line-height: 1.5em;
  background-color: #dde6e1;
  background-color: var(--subtle-green);
  padding: 20px;
}

.flex-container {
  display: flex;
  /* justify-content: space-between; */
}

.flex-column {
  padding: 15px;
}

input[type='text'],
input[type='search'] {
  padding: 5px;
  width: 100%;
}

.hide-input .ReactTags__tagInput {
  display: none;
}

.industries-scrollbox {
  height: 600px;
  overflow: scroll;
  outline: 1px dotted green;
}
